.BannerCommonInnerParent {
  // display: block;
  // background-size: 75% 100%;
  position: relative;
  background-position: right;
  height: 514px;
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, color('background', 1), color('background', 0));
    z-index: 0;
  }
}

@media (max-width:1699px ){
  .BannerCommonInnerParent {
    height: 450px;
  }
}

@media (max-width:1230px ) {
  .BannerCommonInnerParent {
    height: 400px;
  }
}

@media (max-width:991px ) {
  .BannerCommonInnerParent { 
    height: 350px;
  }
}

@media (max-width:767px ) {
  .BannerCommonInnerParent { 
    height: auto;  background-size: 90% 100%;  padding-top: 30px;  padding-bottom: 30px;}
}
